@media (max-width: 1220px){
    .section-container{
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        /* color: var(--Secondary); */
        /* gap: 60px; */
        padding: 140px 20px;
      }
      .section-container span{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 20px;
        height: 120px;
      }
      .section-container p{
        font-size: 70px;
        height: 100px;
        font-weight: 700;
      }
     
     
 
      .lorem{
        width: 600px;
        text-align: center;
        font-size: 18px;
        line-height: 40px;
        font-weight: 500;
        color: var(--nav);
      }








      .carousel-containers {
        max-width: 800px;
        display: flex;
        flex-direction: column;
        margin: auto;
    }
    .carousel-containers button{
        border: none;
    }
    .immm {
        width: 100%;
    }
    
    .dls{
        max-width: 800px;
        display: flex;
        justify-content: space-between;
        gap: 10px;
    }
    .con{
        max-width: 260px;
        height: 300px;
        overflow: hidden;
    }
    .dls img{
        max-width: 260px;
        padding-left: 1px;
    }
    .dls img:hover {
        transform: scale(1.1);
        transition: all .2s linear;
        opacity: 0.8;
    }
    
    
    .carousel-wrappers {
        display: flex;
        width: 1080;
        position: relative;
    }
    
    .carousel-content-wrappers {
        overflow: hidden;
        width: 100%;
        height: 100%;
    }
    
    .carousel-contents {
        display: flex;
        transition: all 250ms linear;
        -ms-overflow-style: none;  /* hide scrollbar in IE and Edge */
        scrollbar-width: none;  /* hide scrollbar in Firefox */
    }
    
    /* hide scrollbar in webkit browser */
    .carousel-content::-webkit-scrollbar, .carousel-content::-webkit-scrollbar {
        display: none;
    }
    
    .carousel-content > * {
        width: 100%;
        flex-shrink: 0;
        flex-grow: 1;
    }
    
    .left-arrow, .right-arrow {
        position: absolute;
        z-index: 1;
        top: 40%;
        transform: translateY(-50%);
        width: 38px;
        height: 38px;
        border-radius: 24px;
        background-color: rgb(212, 206, 206);
        border: 1px solid #ddd;
    }
    
  
    .left-arrow {
        left:  -44px;
        margin-left: 30px;
    }
    
    .right-arrow {
        margin-right: 30px;
        right: -44px;
    }
}

