@media(max-width: 1260px){
  .boxing{
    width: 220px;
    display: flex;
    flex-direction: column;
  }
  .extras{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 20px;
    padding: 0 20px;
  }
  .watches {
    min-height: 220px;
  }

}

@media(max-width: 1200px){
    .shops{
        margin-top: 100px;
        padding: 140px 0;
        display: flex;
        flex-direction: column;
        gap: 30px;
      }
      .top-w{
        display: flex;
        justify-content: space-between;
      }
      .top-w select {
        padding: 15px 30px;
        border-radius: 40px;
        font-size: 18px;
      }
  
      .bask{
        width: 30px;
      }
      .boxing{
        width: 220px;
        display: flex;
        flex-direction: column;
      }
      .extras{
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: repeat(2, 1fr);
        gap: 20px;
        padding: 0 20px;
      }
   
      .boxing .line{
        width: 70%;
        border: 1px solid rgb(187, 186, 186);
      }
      .basket-item{
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
      .ic{
        width: 40px;
        height: 40px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        background-color: #e2e7ee;
      }
      .watches {
        min-height: 220px;
      }
      .price{
        font-size: 18px;
        font-weight: 700;
      }
      
}

