@media (max-width: 1200px) {
  .container {
    max-width: 1200px;
    margin: auto;
    padding: 0 30px;
  }

  .button {
    width: 120px;
    height: 45px;
    font-weight: 700;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 25px;
    color: var(--primary);
    background-color: var(--extra);
    transition: all .3s linear;
  }

  .button:hover {
    background-color: var(--primary);
    border: 2px solid var(--extra);
    color: var(--Secondary);
  }

  .btn-pur {
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 4px;
    border-radius: 6px;
    text-align: center;
    position: absolute;
    left: 12px;
    background-color: var(--extra);
    color: var(--primary);
  }

  .ppp {
    color: black;
  }

  .basket-item-source {

    display: none;
  }

  .basket-item-source img {
    height: 50px;
  }

  .contain {
    width: 100%;
    height: 75%;
    display: flex;
    z-index: 1;
    justify-content: space-between;
    overflow: hidden;
    padding: 20px 20px 100px 20px;
    margin-top: 100px;
  }

  .ddd p {
    font-size: 24px;
    font-weight: 700;
  }

  .ddd span {
    font-size: 18px;
    font-weight: 500;
  }

  .ellips {
    position: absolute;
    right: -700px;
    top: -600px;
    width: 1200px;
  }


  .watching {
    width: 400px;
    height: 80%;
    margin-left: 140px;
    margin-top: 100px;
  }

  .leftside {
    width: 49%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-weight: 500;
  }

  .leftside h1 {
    line-height: 90px;
    font-size: 90px;
  }

  .leftside p {
    font-weight: 800;
    font-size: 18px;
  }

  .rightside {
    padding-right: 10px;
    display: flex;
    z-index: 9;
    --percent: 0%;
  }



  .lefts {
    max-width: 400px;
    padding-left: 30px;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  .lefts p {
    max-width: 350px;
    font-size: 18px;
    font-weight: 500;
  }

  .ff {
    padding-left: 100px;
  }

  .navs img {
    width: 40px;
  }

  .navs {
    display: flex;
    gap: 10px;
  }

  .security {
    display: flex;
    align-items: center;
    gap: 10px;
  }

  .security img {
    height: 30px;
  }

  .footer {
    display: flex;
    justify-content: space-between;
  }

  .f-last {
    text-align: center;
    margin-top: 40px;
  }



}

