.carousel-containers {
    width: 1071px;
    display: flex;
    flex-direction: column;
    
}
.carousel-containers button{
    border: none;
}
.immm {
    width: 100%;
    display: flex;
    justify-content: center;
}

.dls{
    width: 1080px;
    display: flex;
    justify-content: space-between;
    gap: 10px;
}
.con{
    max-width: 350px;
    height: 400px;
    overflow: hidden;
}
.dls img{
    max-width: 350px;
    padding-left: 1px;
}
.dls img:hover {
    transform: scale(1.1);
    transition: all .2s linear;
    opacity: 0.8;
}


.carousel-wrappers {
    display: flex;
    width: 1080;
    position: relative;
}

.carousel-content-wrappers {
    overflow: hidden;
    width: 100%;
    height: 100%;
}

.carousel-contents {
    display: flex;
    transition: all 250ms linear;
    -ms-overflow-style: none;  /* hide scrollbar in IE and Edge */
    scrollbar-width: none;  /* hide scrollbar in Firefox */
}

/* hide scrollbar in webkit browser */
.carousel-content::-webkit-scrollbar, .carousel-content::-webkit-scrollbar {
    display: none;
}

.carousel-content > * {
    width: 100%;
    flex-shrink: 0;
    flex-grow: 1;
}

.left-arrow, .right-arrow {
    position: absolute;
    z-index: 1;
    top: 50%;
    transform: translateY(-50%);
    width: 48px;
    height: 48px;
    border-radius: 24px;
    background-color: white;
    border: 1px solid #ddd;
}

.left-arrow {
    left:  -44px;
}

.right-arrow {
    right: -44px;
}