@media(max-width: 1200px){
    .mainfirst{
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        padding-top: 140px;
      }
      .tandem{
        display: flex;
        align-items: center;
        height: 50px;
        gap: 20px;
        margin-bottom: 10px;
      }
      .tandem img{
        align-self: flex-start;
        width: 30px;
           filter: invert(12%) sepia(83%) saturate(5841%) hue-rotate(244deg) brightness(87%) contrast(153%);
        padding-top: 0px;
      }
      .tand{
        display: flex;
        flex-direction: column;
        /* gap: 10px; */
        /* line-height: 0; */
      }
      .tand h3{
        height: 0px;
      }
      .tand p{
        height: 0px;
      }
      .text span{
        font-size: 20px;
        font-weight: 700;
        color: grey;
      }
    
      .percent{
        width: 445px;
        height: 5px;
        display: flex;
        justify-content: space-evenly;
        border-radius: 100px;
      }
      .mainthree{
        display: flex;
        gap: 30px;
      }
      .tandems{
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-top: 30px;
      }
      .percentage{
        display: flex;
        flex-direction: column;
        align-items: flex-end;
      }
      
      .active{
        border-radius: 100px;
        /* width: var(--percent); */
        width: 60%;
        background-color: #34547A;
        height: 100%;
      
       
      }
      .passive{
        /* width: calc(100% - var(--percent)); */
        width: 40%;
        background: #C4C4C4;
        height: 100%;
        border-top-right-radius: 100px;
        border-bottom-right-radius: 100px ;
      }
      
      .videos{
        display: flex;
        flex-direction: column;
        padding: 100px 20px;
        gap: 20px;
      }
      .how{
        font-weight: 700;
        font-size: 24px;
        color: gray;
      }
      .blue{
        color: blue;
      }
      
      .videos span{
        font-size: 60px;
        font-weight: 900;
      }
      .videos p{
        width: 400px;
      }
      .videos iframe{
        width: 70%;
        height: 70vh;
        margin: auto;
      }
      .blackscreen{
        background-color: #363636;
        color: #fff;
      }
      .black-container{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 100px 10px;
      }
      .black-container p{
        width: 600px;
        font-weight: 800;
        font-size: 50px;
        text-align: center;
      }
      .carusel-two{
        display: flex;
        width: 900px;
        gap: 10px;
      }
      .item-c-one img{
        width: 300px;
      }

       
      .br{
        display: flex;
        flex-direction: column;
        gap: 30px;
      }
}

