@media(max-width: 1200px){

    .reviews{
        margin-top: 100px;
        display: flex;
        flex-direction: column;
        gap: 30px;
        align-items: center;
        padding: 100px 0;
        
      }
      .reviews h1{
        text-align: center;
        width: 500px;
        font-size: 60px;
      }

    .carousel-container {
        width: 140px;
        /* height: 100px; */
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    
    .cryt{
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 20px;
    }
    .cryt .et{
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 30px;
    }
  
    .et p{
      max-width: 600px;
      text-align: center;
      font-size: 20px;
      color: var(--nav);
    }
    .et span{
      font-size: 22px;
      font-weight: 700;
      color: var(--extra);
    }
    
    
    .carousel-wrapper {
        display: flex;
        width: 100%;
        position: relative;
    }
    
    .carousel-content-wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        width: 100%;
        height: 100%;
        border-radius: 50%;
    }
    
    .carousel-content {
        display: flex;
        transition: all 250ms linear;
        -ms-overflow-style: none;  /* hide scrollbar in IE and Edge */
        scrollbar-width: none;  /* hide scrollbar in Firefox */
    }
    
    /* hide scrollbar in webkit browser */
    .carousel-content::-webkit-scrollbar, .carousel-content::-webkit-scrollbar {
        display: none;
    }
    
    .carousel-content > * {
        width: 100%;
        flex-shrink: 0;
        flex-grow: 1;
    }
    .left-arrow, .right-arrow {
        position: absolute;
        z-index: 1;
        top: 50%;
        transform: translateY(-50%);
        width: 68px;
        height: 68px;
        border-radius: 50%;
        /* background-color: rgba(0, 58, 146, 0.377); */
        border: 1px solid #ddd;
    }
    
    .left-arrow img{
        width: 30px;
    }
    .right-arrow  img{
        width: 30px;
    }
    .left-arrow {
        left: -130px;
    
    }
    
    .right-arrow {
        right: -130px;
    }
    
    
    
    
    .App {
        text-align: center;
      }
      
      .App-logo {
        height: 40vmin;
        pointer-events: none;
      }
      
      @media (prefers-reduced-motion: no-preference) {
        .App-logo {
          animation: App-logo-spin infinite 20s linear;
        }
      }
      
      .App-header {
        background-color: #282c34;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        font-size: calc(10px + 2vmin);
        color: white;
      }
      
      .App-link {
        color: #61dafb;
      }
      
      @keyframes App-logo-spin {
        from {
          transform: rotate(0deg);
        }
        to {
          transform: rotate(360deg);
        }
      }
      
    .et div{
        font-size: 24px;
        font-weight: 700;
    }
      
      .abb img{
        width: 140px;
        height: 140px;
      }
}
@media(max-width: 640px){
  
    .et p{
      max-width: 400px;
      text-align: center;
      font-size: 20px;
      color: var(--nav);
    }
    
    .left-arrow img{
        width: 30px;
    }
    .right-arrow  img{
        width: 30px;
    }
   
}
