@media (max-width: 1200px ){
    

header {
    width: 100%;
    height: 100px;
    padding: 0 20px;
    position: fixed;
    background-color: rgb(94, 86, 86);
    z-index: 999;
  }
  .header-container{
    max-width: 1200px;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: auto;
  }
  .fullScreen {
    width: 100%;
    min-height: 100vh;
  }
  
  .hero-container {
    overflow: hidden;
    position: relative;
    width: 100%;
    min-height: 100vh;
    /* background-color: black; */
  }
  
  .container {
    max-width: 1310px;
    margin: auto;
    padding: 0 30px;
  }
  
  .logo img {
    width: 80px;
    background: transparent;
  }
  
  nav {
    display: flex;
    gap: 20px;
  }
  
  nav a {
    color: var(--primary);
    text-decoration: none;
    font-size: 16px;
    font-weight: 600;
    padding: 4px 8px;
    border-radius: 10px;
    transition: all .2s linear;
  }
  
  nav a:hover {
   transform: scale(.96);
   color: var(--nav);
  }
  
  .nav-link {
    position: relative;
  }
 
  
  .nav-link img {
    width: 20px;
    margin-top: 8px;
    filter: invert(100%) sepia(10%) saturate(0%) hue-rotate(0deg) brightness(100%) contrast(115%)
  }
  
  .nav-link a{
    width: 1px;
    padding-right: 20px;
    background-color: transparent;
  }
  
  .button {
    width: 100px;
    height: 45px;
    font-weight: 700;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 25px;
    color: var(--primary);
    background-color: var(--extra);
    transition: all .3s linear;
  }
  .dl img{
    width: 35px;
    filter: invert(28%) sepia(99%) saturate(2476%) hue-rotate(96deg) brightness(128%) contrast(179%);
  
  }
  .button:hover {
    background-color: var(--primary);
    border: 2px solid var(--extra);
    color: var(--Secondary);
  }
  
  .btn-pur {
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 4px;
    border-radius: 6px;
    text-align: center;
    position: absolute;
    left: 12px;
    background-color: var(--extra);
    color: var(--primary);
  }
  .ppp{
    color: black;
  }
  .basket-item-source{
  
    display: none;
  }
  .basket-item-source img{
      height: 50px;
  }
}